// Breakpoints helpers

// 320px
@mixin bp-xs {
  @media screen and (min-width: 320px) {
    @content; } }

// 480px
@mixin bp-sm {
  @media screen and (min-width: 480px) {
    @content; } }

// 768px
@mixin bp-md {
  @media screen and (min-width: 768px) {
    @content; } }

// 992px
@mixin bp-lg {
  @media screen and (min-width: 992px) {
    @content; } }

// 1200px
@mixin bp-xl {
  @media screen and (min-width: 1200px) {
    @content; } }

@mixin bp-custom($width, $px) {
  @media screen and (#{$width}-width: $px) {
    @content; } }

// Keyframes
@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content; }
  @-moz-keyframes #{$name} {
    @content; }
  @-ms-keyframes #{$name} {
    @content; }
  @keyframes #{$name} {
    @content; } }
