* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

a, a:hover {
  text-decoration: none; }

body {
  font-family: $primary-font;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 19px;
  line-height: 20px;
  font-weight: bold;
  color: $white;
  background: rgba(27,40,52,1);
  background: -moz-radial-gradient(center, ellipse cover, rgba(27,40,52,1) 0%, rgba(14,19,26,1) 100%);
  background: -webkit-gradient(radial, center center, 0px, center center, 100%, color-stop(0%, rgba(27,40,52,1)), color-stop(100%, rgba(14,19,26,1)));
  background: -webkit-radial-gradient(center, ellipse cover, rgba(27,40,52,1) 0%, rgba(14,19,26,1) 100%);
  background: -o-radial-gradient(center, ellipse cover, rgba(27,40,52,1) 0%, rgba(14,19,26,1) 100%);
  background: -ms-radial-gradient(center, ellipse cover, rgba(27,40,52,1) 0%, rgba(14,19,26,1) 100%);
  background: radial-gradient(ellipse at center, rgba(27,40,52,1) 0%, rgba(14,19,26,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#1b2834', endColorstr='#0e131a', GradientType=1 );
  width: 100%;
  height: 100%;
  position: relative;
  &::before, &::after {
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    -webkit-animation-name: stars-moving;
    -webkit-animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
    position: absolute;
    background-repeat: repeat;
    z-index: -1; }
  &::before {
    background-image: url(../images/bg-stars-one.png);
    -webkit-animation-duration: 50s; }
  &::after {
    background-image: url(../images/bg-stars-two.png);
    -webkit-animation-duration: 100s; }
  &.home-page {
    overflow: hidden;
    height: 100vh; } }

h1, h2, h3, h4, h5, h6 {
  margin: 0; }

img {
  max-width: 100%;
  height: auto; }
